<template>
	<div id="shopSelect">
		<!--  -->
		<header-title header-title=""></header-title>
		<!--  -->
		<div class="ssCon">
			<div>
				<flex-to-page span-text="" link-to="SetUp"></flex-to-page>
				<div class="ssConShopMaterial">
					<div></div>
					<p></p>
				</div>
			</div>
			<div>
				<flex-to-page span-text="" link-to="PatternLogin"></flex-to-page>
				<div class="ssConShopMaterial">
					<div></div>
					<p><br /></p>
				</div>
			</div>
			<div>
				<flex-to-page span-text="" link-to="BasedDetails"></flex-to-page>
				<div class="ssConShopMaterial">
					<div></div>
					<p><br /></p>
				</div>
			</div>
		</div>
		<!--  -->
		<div class="ssDownload">
			<a href="javascript:void(0);" @click="openInputResult"></a><span>|</span><a href="javascript:void(0);" @click="openAbout"></a>
		</div>
	</div>
</template>

<script>
	import HeaderTitle from "./HeaderTitle"
	import FlexToPage from "./FlexToPage"
	export default {
		name:"ShopSelection",
		data () {
			return {
				
			}
		},
		components:{
			HeaderTitle,
			FlexToPage
		},
		methods:{
			openInputResult:function () {
				this.$router.push("/InputResult")
			},
			openAbout:function () {
				this.$router.push("/About")
			}
		}
		
	}
</script>

<style scoped="scoped">
.ssCon>div {
    margin-bottom: 16px;
    background: #FFFFFF;
}
.ssConShopMaterial {
    text-align: left;
    padding: 0 12px;
}
.ssConShopMaterial>div {
    margin: 16px 0;
    font-size: 16px;
    color: #333333;
}
.ssConShopMaterial>p {
    font-size: 14px;
    color: #FE6B1C;
    padding-bottom: 20px;
}
.ssDownload {
    height: 50px;
    background: #ffffff;
    line-height: 50px;
    margin-top: 32px;
}
.ssDownload>a {
    display: inline-block;
    width: calc((100% - 2px)/2);
    color: #549AFF;
    text-decoration: none;
    font-size: 14px;
}
.ssDownload>span {
    display: inline-block;
    width: 2px;
    color: #979797;
}
</style>


